.article-itemt {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  margin-bottom: 24px;
  align-content: start;
  position: relative;
}
.article-itemt .article-thumb {
  width: 100px;
  flex-shrink: 0;
  position: relative;
  overflow: hidden;
}
.article-itemt .article-thumb .ic-play {
  width: 36px;
  height: 36px;
  line-height: 36px;
  font-size: 14px;
  margin: -18px 0 0px -18px;
}
.article-itemt .article-content {
  display: flex;
  flex-wrap: wrap;
  align-content: start;
  gap: 8px;
  width: calc(100% - 112px);
}
.article-itemt .article-content .date {
  color: #004FC2;
  font-size: 14px;
  display: flex;
  align-items: center;
}
.article-itemt .article-content .date svg {
  margin-right: 5px;
}
.article-itemt .article-content .tag {
  color: #0000EE;
  font-size: 12px;
  text-transform: uppercase;
}
.article-itemt .article-content .title_new {
  font-size: 15px;
  font-weight: 500;
  line-height: 1.5;
  margin-bottom: 0;
  width: 100%;
}
.article-itemt .article-content .teacher {
  font-size: 12px;
  font-weight: 500;
  color: #656867;
}
.article-itemt .article-content .des {
  font-size: 16px;
  color: #6A6377;
  line-height: 1.6;
  margin-bottom: 0;
}
.article-itemt .article-content .des p:last-child {
  margin-bottom: 0;
}
.article-itemt.in-title .article-thumb {
  width: 100%;
}
.article-itemt.in-title .article-content {
  position: absolute;
  padding: 10px;
  bottom: 0;
  width: 100%;
}
.article-itemt.in-title .article-content .title_new {
  font-size: 14px;
  color: #fff;
}
.article-itemt.full .article-thumb {
  width: 100%;
}
.article-itemt.full .article-thumb .ic-play {
  width: 56px;
  height: 56px;
  line-height: 56px;
  font-size: 20px;
  margin: -28px 0 0px -28px;
}
.article-itemt.full .article-content {
  width: 100%;
}
.article-itemt.big .article-content .title_new {
  font-size: 24px;
}
@media (max-width: 991px) {
  .article-itemt.big .article-content .title_new {
    font-size: 18px;
  }
}
.article-itemt.video-top .article-thumb {
  width: 100%;
  border-radius: 16px;
}
.article-itemt.video-top .title_new {
  font-size: 24px;
  color: var(--colormain);
}
@media (max-width: 991px) {
  .article-itemt.video-top .title_new {
    font-size: 18px;
    font-weight: bold;
  }
}
@media (max-width: 991px) {
  .article-itemt .article-thumb {
    width: 140px;
  }
}

.list-course {
  column-gap: 30px;
  row-gap: 50px;
}
.list-course .article-itemt {
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid #DBE2DF;
  background: #FBFDFC;
  cursor: pointer;
  transition: 0.5s;
}
.list-course .article-itemt .article-content {
  padding: 0 12px 12px 12px;
  gap: 15px;
}
.list-course .article-itemt .ic-flex {
  font-weight: 300;
  padding: 0 0 0 25px;
}
.list-course .article-itemt .phan-xa {
  color: var(--color);
  background: url(images/icons/ic-nao.png) no-repeat;
}
.list-course .article-itemt .muc-tieu {
  color: #A674F8;
  background: url(images/icons/ic-muctieu.png) no-repeat;
}
.list-course .article-itemt .update-new {
  color: #F9A826;
  background: url(images/icons/ic-star.png) no-repeat;
}
.list-course .article-itemt .selected {
  color: #A674F8;
  background: url(images/icons/ic-selected.png) no-repeat;
}
.list-course .article-itemt .bar {
  border-radius: 50px;
  background: #F1F8F5;
  height: 5px;
  width: 100%;
  position: relative;
}
.list-course .article-itemt .bar span {
  border-radius: 50px;
  background: linear-gradient(279deg, #A0D9BA 0%, #2E8B57 100%);
  position: absolute;
  height: 5px;
  left: 0;
}
.list-course .article-itemt .bottom-flex {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  gap: 5px;
}
.list-course .article-itemt .bottom-flex .flex {
  display: flex;
  align-items: center;
  gap: 4px;
}
.list-course .article-itemt .bottom-flex .btn-gradient {
  height: 28px;
  padding: 0 6px;
}
.list-course .article-itemt .bottom-flex .btn-gradient span {
  font-size: 13px;
}
.list-course .article-itemt .bottom-flex .btn-default {
  font-size: 13px;
  height: 28px;
  padding: 0 7px;
  border-radius: 2px;
  background: var(--colormain);
  color: #fff;
}
.list-course .article-itemt .bottom-flex .btn-default span {
  font-size: 13px;
}
.list-course .article-itemt .bottom-flex .free {
  font-weight: 300;
  border-radius: 2px;
  color: var(--colormain);
  background: #F1F8F5;
  padding: 4px 8px;
}
.list-course .article-itemt .bottom-flex .view {
  font-weight: 300;
  display: flex;
  align-items: center;
  gap: 4px;
  color: #656867;
}
.list-course .article-itemt .bottom-flex.line-top {
  border-top: 1px solid #DBE2DF;
  padding-top: 15px;
}
.list-course .article-itemt:hover {
  transform: translateY(-10px);
  box-shadow: 2px 4px 10px 0 rgba(0, 0, 0, 0.2);
}
@media (max-width: 1920px) {
  .list-course.grid__4 .article-itemt:nth-child(9), .list-course.grid__4 .article-itemt:nth-child(10) {
    display: none;
  }
  .list-course.folder .article-itemt:nth-child(9), .list-course.folder .article-itemt:nth-child(10) {
    display: flex;
  }
}
@media (min-width: 1921px) {
  .list-course.grid__4 {
    grid-template-columns: repeat(5, 1fr);
  }
}
@media (max-width: 1700px) {
  .list-course.grid {
    grid-template-columns: repeat(3, 1fr);
    row-gap: 30px;
  }
  .list-course.grid .article-itemt:nth-child(9) {
    display: flex;
  }
}
@media (max-width: 1480px) {
  .list-course .article-itemt .article-content {
    padding: 0 10px 10px 10px;
  }
  .list-course .article-itemt .bottom-flex .flex {
    gap: 4px;
  }
}
@media (max-width: 1240px) {
  .list-course .article-itemt .bottom-flex .btn-default {
    display: none;
  }
}
@media (max-width: 1025px) {
  .list-course .article-itemt .bottom-flex .btn-default {
    display: flex;
  }
  .list-course.grid {
    column-gap: 20px;
    row-gap: 20px;
  }
}
@media (max-width: 991px) {
  .list-course.grid {
    grid-template-columns: repeat(2, 1fr);
  }
  .list-course.grid .article-itemt:nth-child(10) {
    display: flex;
  }
}
@media (max-width: 600px) {
  .list-course .article-itemt {
    gap: 8px;
  }
  .list-course .article-itemt .article-content {
    gap: 8px;
    padding: 0 8px 8px 8px;
  }
  .list-course .article-itemt .article-content .title_new {
    font-size: 13px;
  }
  .list-course .article-itemt .bar {
    height: 3px;
  }
  .list-course .article-itemt .bar span {
    height: 3px;
  }
  .list-course .article-itemt .ic-flex {
    font-size: 12px;
    background-size: 18px;
    padding-left: 22px;
  }
  .list-course .article-itemt .bottom-flex {
    flex-wrap: wrap;
  }
  .list-course .article-itemt .bottom-flex .flex {
    width: 100%;
  }
  .list-course .article-itemt .bottom-flex .btn-gradient {
    width: calc(50% - 2px);
  }
  .list-course .article-itemt .bottom-flex .btn-gradient span {
    font-size: 12px;
    gap: 2px;
  }
  .list-course .article-itemt .bottom-flex .btn-gradient span .ic {
    width: 16px;
    height: 16px;
    background-size: contain !important;
  }
  .list-course .article-itemt .bottom-flex .btn-gradient.live span {
    font-size: 0;
    position: relative;
  }
  .list-course .article-itemt .bottom-flex .btn-gradient.live span::before {
    content: "Live";
    font-size: 13px;
  }
  .list-course .article-itemt .bottom-flex .btn-gradient.video .ic {
    margin-top: 2px;
  }
  .list-course .article-itemt .bottom-flex .btn-default {
    width: 100%;
  }
  .list-course.grid {
    column-gap: 15px;
    row-gap: 15px;
  }
  .list-course.horizontal {
    grid-template-columns: repeat(1, 1fr);
    background: #fff;
    padding: 6px;
    column-gap: 12px !important;
    row-gap: 12px !important;
  }
  .list-course.horizontal .article-itemt {
    gap: 12px;
    flex-wrap: nowrap;
    border: 0;
    border-radius: 0;
  }
  .list-course.horizontal .article-itemt.article-itemt + .article-itemt {
    border-top: 1px dashed #DBE2DF;
    padding-top: 12px;
  }
  .list-course.horizontal .article-itemt .article-thumb {
    width: 140px;
    flex-shrink: 0;
  }
  .list-course.horizontal .article-itemt .article-content {
    padding: 0;
  }
  .list-course.horizontal .article-itemt .article-content .title_new {
    font-size: 14px;
  }
  .list-course.horizontal .article-itemt .ic-flex {
    display: none;
  }
  .list-course.horizontal .article-itemt .bottom-flex {
    padding-top: 0;
    border-top: 0;
    flex-wrap: nowrap;
  }
  .list-course.horizontal .article-itemt .bottom-flex .flex {
    width: auto;
  }
  .list-course.gap_16 .bottom-flex {
    flex-wrap: nowrap;
  }
  .list-course.gap_16 .bottom-flex .flex {
    width: auto;
  }
  .list-course.gap_16 .bottom-flex .btn-gradient {
    width: auto;
  }
  .list-course.gap_16 .bottom-flex .btn-gradient.video span {
    font-size: 0;
  }
}
@media (max-width: 400px) {
  .list-course.grid {
    column-gap: 10px !important;
    row-gap: 10px !important;
  }
}